import React from "react";
import Grid from "./Grid"


export const Quiz =  (props)=> {
  return (
    <div className="quiz">
      <div className="quiz-board">
        <div className="status"></div>
          <Grid renderQuestion={props.renderQuestion()}/>
        <div className="bg-primary"> Your score is {props.renderScore()} </div>
      </div>
      <div className="game-info">
        <div>{/* status */}</div>
        <ol>{/* TODO */}</ol>
      </div>
    </div>
  );
}



