export const boardReducer = (state={score:0, ans:0, status:'progress'}, action) => {
	switch(action.type) {
		case "ANSWER":
			state = {...state, 
				ans:state.ans+1};
			break;

		case "END":
			state = {...state, 
				status: "Finished"};
			break;	
		case "SCORE":
			state = {...state,
				score: state.score+1}
			break;
		default:
			return state;		
	}
	return state;

}




