import React from "react"


const Grid =  (props) => {
	return (
        <div>
          {props.renderQuestion(0)}
          {props.renderQuestion(1)}
          {props.renderQuestion(2)}
          {props.renderQuestion(3)}
          {props.renderQuestion(4)}
          {props.renderQuestion(5)}
          {props.renderQuestion(6)}
          {props.renderQuestion(7)}
          {props.renderQuestion(8)}
          {props.renderQuestion(9)}
        </div>

    );
}

export default Grid;


