import Immutable from "immutable"

export const answerReducer = (state={
    answers: Immutable.List(Array(9).fill(null)), 
    check:Immutable.List(Array(9).fill(null))}, action) => {

	switch(action.type) {
		case "COMMIT":
            var new_answers = Immutable.List(state.answers.set(action.id,action.payload))
            var new_check = Immutable.List(state.check.set(action.id,true))
			state = {...state,
				answers:new_answers,
				check:new_check}
            return state;

		default:
			return state;
	}
	
}

