import React, { Component } from 'react';
import {Quiz}  from '../components/Quiz'
import { connect } from "react-redux";
import {answerAct, endAct, scoreAct} from "../actions/boardActions";
import {commitAct} from "../actions/answerActions";
import QA from "../components/dumb/QA"
import Question from "../components/Question"


var data = QA();

class App extends Component {

  handleClick(e,id){
    if (!this.props.answerS.check[id]) {
      this.props.answeR();
    }
    var val = e.target.value
    this.props.commiT(id,val)

  }

  componentWillReceiveProps(nextProps) {
    if (this.props.boardS.ans===9 && nextProps.boardS.ans===10){
        for (var i = data.length - 1; i >= 0; i--) {
          if (data[i].a===nextProps.answerS.answers.get(i)) {
            this.props.scorE();
          } 
        }
        this.props.enD();

      }
    }


  renderScore() {
    if (this.props.boardS.status==="Finished") {
      return this.props.boardS.score
    }else{
      return "not calculated yet."
    }
  }

  renderQuestion(i) {
    return <Question id={i}
      question={data[i].q} answer={data[i].o} onClick={this.handleClick.bind(this)} />;
  }
  

  render() {
    return (
      <div className="App">
        <div className="App-header">
          <h2>Welcome to Quiz</h2>
        </div>

        <Quiz renderScore={this.renderScore.bind(this)} renderQuestion={(i) => this.renderQuestion.bind(this)}  />
      </div>
    );
  }
}

const mapStateToProps = (state)=> {
  return {
    answerS: state.answerReducer,
    boardS: state.boardReducer
  }
}
const mapDispatchToProps = (dispatch)=> {
  return {
    answeR: () => {dispatch(answerAct())},
    enD: () => {dispatch(endAct())},
    scorE: () => {dispatch(scoreAct())},
    commiT: (i,value) => {dispatch(commitAct(i,value))}
  }
}
export default connect(mapStateToProps,mapDispatchToProps) (App);



