import React from "react";


export default function Question (props) {
  return (
    <div className="">


      <div className="row">
        <div className="col-xs-12">
      <br/> <h4>{props.question}</h4>
               <div className="btn-group btn-group-vertical" data-toggle="buttons">
                  <label className="btn active">
                    <input type="radio" name={props.id} value="0" onClick={(e)=>props.onClick(e, props.id)} />
                      <i className="fa fa-circle-o fa-2x"></i>
                      <i className="fa fa-dot-circle-o fa-2x"></i>
                      <span>  {props.answer[0]} </span>
                  </label>

                  <label className="btn active">
                    <input type="radio" name={props.id} value="1" onClick={(e)=>props.onClick(e, props.id)} />
                      <i className="fa fa-circle-o fa-2x"></i>
                      <i className="fa fa-dot-circle-o fa-2x"></i>
                      <span>  {props.answer[1]} </span>
                  </label>


                  <label className="btn active">
                    <input type="radio" name={props.id} value="2" onClick={(e)=>props.onClick(e, props.id)} />
                      <i className="fa fa-circle-o fa-2x"></i>
                      <i className="fa fa-dot-circle-o fa-2x"></i>
                      <span>  {props.answer[2]} </span>
                  </label>
              </div>

    		</div>


  		</div>
	</div>
  );
}





